@font-face {
    font-family: PostGrotesk;
    src: url(./fonts/PostGrotesk-Medium.otf);
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.App {
    background: white;
    width: 100vw;
    height: 100vh;
    font-family: PostGrotesk;
    font-size: 25px;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 30s linear;
            animation: App-logo-spin infinite 30s linear;
    position: fixed;
    width: 400px;
    height: 400px;
    left: calc(50vw - 200px);
    top: calc(50vh - 200px);
}

#logo:hover {
    opacity: 0;
}

#logo {
    opacity: 1;
}

#logo-yellow:hover {
    opacity: 1;
}

#logo-yellow {
    opacity: 0;
}

#footerContainer {
    bottom: 0;
    width: 100vw;
    position: fixed;
    line-height: 2em;
    margin: auto auto;
    font-family: PostGrotesk;
    font-size: 0.75em;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: middle;
        align-items: middle;
    -ms-flex-pack: center;
        justify-content: center;
    padding-bottom: 1em;
}

a {
    color: black;
    text-decoration: none;
    padding: 0 1em;
}

a:hover {
    color: rgb(100, 96, 31);
}

a:visited {
    color: black;
}

a:active {
    color: black;
}

a:visited {
    color: black;
}

a:visited:hover {
    color: rgb(100, 96, 31);
}

@media only screen and (max-width:670px) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
        position: fixed;
        text-align: center;
        width: 90vw;
        left: calc(50vw - 45vw);
        bottom: calc(40vh - 45vw);
    }
}


